.ghost-profiles {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
}

.ghost-profile {
  padding: 3px;
  border: 1px solid grey;
  border-radius: 5px;
  width: 150px;
}
.ghost-profile > div {
  margin-bottom: 3px;
}

.evidence-bubble {
  padding: 2px;
  border-radius: 5px;
  background: lightgrey;
  width: fit-content;
}